//历史理赔记录
import React, { Component } from 'react';
import { Table, Divider } from 'antd';
import Item from 'antd/lib/list/Item';

const dataSource = [
  {
    key: '1',
    id: '1111111111',
    time: '2019.1.1',
    money: '壹万元整',
    info: '下载',
  },
  {
    key: '2',
    id: '2222222222',
    time: '2018.9.8',
    money: '肆万元整',
    info: '下载',
  },
  {
    key: '3',
    id: '3333333333',
    time: '2018.3.4',
    money: '伍万元整',
    info: '下载',
  },
  {
    key: '4',
    id: '',
    time: '',
    money: '',
    info: '',
  },
  {
    key: 5,
    id: '',
    time: '',
    money: '',
    info: '',
  },
  {
    key: 6,
    id: '',
    time: '',
    money: '',
    info: '',
  },
];

const columns = [
  {
    title: '保单号',
    dataIndex: 'id',

  },
  {
    title: '理赔时间',
    dataIndex: 'time',
 
  },
  {
    title: '赔付金额',
    dataIndex: 'money',
  },
  {
    title: '详情',
    dataIndex: 'info',
    render: text=>{
      return (
        <a href="#">{text}</a>
      );
    }

  }
];

export class Records extends Component{
    constructor(props){
      super(props); 

    }
    data = dataSource;
    dataSearch=[];
    render() {
        this.dataSearch=[];
        if(this.props.value!=''){
          this.data.map(Item=>{
              if(Item.id==this.props.value)
                  this.dataSearch.push(Item)
          })
          return (
            <div>
              <Table columns={columns} dataSource={this.dataSearch} pagination={false} bordered scroll={{x: 60, y: 180 }} />
            </div>
          );
        }
        else {
          return (
            <div>
                <Table columns={columns} dataSource={dataSource} pagination={false} bordered scroll={{x: 60, y: 180 }} />
            </div>
          );
        }
    }

}
